import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store'
import { FormControl, FormGroup, FormGroupDirective, NgForm, FormBuilder, Validators } from '@angular/forms'
import { ErrorStateMatcher } from '@angular/material/core';

@Component({
  selector: 'app-tezos-baking',
  templateUrl: './tezos-baking.component.html',
  styleUrls: ['./tezos-baking.component.scss']
})
export class TezosBakingComponent implements OnInit {

  public landing
  public landingForm
  public emailLandingErrorMatcher = new EmailLandingErrorStateMatcher();

  constructor(
    private store: Store<any>,
    public fb: FormBuilder,
  ) { }

  ngOnInit() {

    // initialize component
    this.store.dispatch({
      type: "LANDING_INIT",
    })

    // initilize form
    this.landingForm = this.fb.group({
      email: ['', [
        Validators.required,
        Validators.email,
      ]]
    })

    // listen to formData change
    this.landingForm.valueChanges.subscribe(landingFormData => {
      this.store.dispatch({ type: "LANDING_FORM_CHANGE", payload: landingFormData })
    })

  }

  landingSignUp() {

    // mark input 
    this.landingForm.controls.email.markAsTouched()

    // check validity
    this.landingForm.updateValueAndValidity()

    // dispatch only if valid
    if (this.landingForm.valid) {

      // landing sign up
      this.store.dispatch({
        type: "LANDING_SIGN_UP",
      })

    }

  }
  
}

// Error when invalid control is dirty, touched, or submitted. 
export class EmailLandingErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
