import { Component, OnInit, OnDestroy } from '@angular/core';
import { Store } from '@ngrx/store'
import { FormControl, FormGroup, FormGroupDirective, NgForm, FormBuilder, Validators } from '@angular/forms'
import { ErrorStateMatcher } from '@angular/material/core';

import { Subject, of } from 'rxjs';
import { takeUntil } from 'rxjs/operators';


@Component({
  selector: 'app-tezos-hardware-wallet',
  templateUrl: './tezos-hardware-wallet.component.html',
  styleUrls: ['./tezos-hardware-wallet.component.scss']
})
export class TezosHardwareWalletComponent implements OnInit {

  public landing
  public landingForm
  public emailLandingErrorMatcher = new EmailLandingErrorStateMatcher();
  public destroy$ = new Subject<null>();

  constructor(
    private store: Store<any>,
    public fb: FormBuilder,
  ) { }

  ngOnInit() {

    // initilize form
    this.landingForm = this.fb.group({
      email: ['', [
        Validators.required,
        Validators.email,
      ]]
    })

    // listen to landing state
    this.store.select('landingTezosHardwareWallet')
      .pipe(takeUntil(this.destroy$))
      .subscribe(state => {
        this.landing = state
      })

  }

  ngOnDestroy() {

    // close all open directives
    this.destroy$.next();
    this.destroy$.complete();

    // destroy tezos transaction component
    this.store.dispatch({
      type: 'LANDING_DESTROY',
      payload: '',
    })

  }

  landingSubscribe() {

    // mark input 
    this.landingForm.controls.email.markAsTouched()

    // check validity
    this.landingForm.updateValueAndValidity()

    // dispatch only if valid
    if (this.landingForm.valid) {

      // landing sign up
      this.store.dispatch({
        type: "LANDING_SUBSCRIBE",
      })

    }

  }

}

// Error when invalid control is dirty, touched, or submitted. 
export class EmailLandingErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
