import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Effect, Actions, ofType } from '@ngrx/effects';
import { Router, ActivatedRoute } from '@angular/router';
import { Store } from '@ngrx/store';
import { of } from 'rxjs';
import { withLatestFrom, flatMap, filter, map, tap, catchError } from 'rxjs/operators';

import { AngularFirestore } from 'angularfire2/firestore';

@Injectable()
export class TezosHardwareWalletEffects {

    public emailListCol
    public emailList

    @Effect()
    langingHardwareWalletSubscribe$ = this.actions$.pipe(
        ofType('LANDING_SUBSCRIBE'),
        // get state from store
        withLatestFrom(this.store, (action, state: any) => state),

        flatMap(state => {
            // listen to email_list from FireBase 
            this.emailListCol = this.db.collection('email_list');
            this.emailList = {
                email: state.landingTezosHardwareWallet.form.email,
            }
            // add value to firestore
            return this.emailListCol
                // set document id as email
                .doc(state.landingTezosHardwareWallet.form.email)
                .set(this.emailList)

        }),

        // dispatch action based on result
        map((data: any) => ({
            type: 'LANDING_SUBSCRIBE_SUCCESS',
            payload: { ...data }
        })),
        catchError((error, caught) => {
            console.error(error.message)
            this.store.dispatch({
                type: 'LANDING_SUBSCRIBE_ERROR',
                payload: error.message,
            });
            return caught;
        }),

        // redirect back to accounts list
        // tap(() => this.router.navigate(['/tezos/wallet']))
    )

    //TODO: refactor and use interceptor for firebase 
    @Effect()
    langingHardwareWalletProgerssShow$ = this.actions$.pipe(
        ofType('LANDING_SUBSCRIBE'),
        map((data: any) => ({ type: 'PROGRESSBAR_SHOW' })),
    )

    @Effect()
    langingHardwareWalletProgerssHide$ = this.actions$.pipe(
        ofType('LANDING_SUBSCRIBE_SUCCESS'),
        // send tracking code after email
        tap(() => {
            (<any>window).ga('gtag_UA_114065646_1.send', 'event', 'landing_hw_wallet_coming', 'email_subscription')
                (<any>window).dataLayer.push({ 'event': 'gtag_email_subscription' })

        }),
        map((data: any) => ({ type: 'PROGRESSBAR_HIDE' })),
    )

    @Effect()
    langingHardwareWalletErrorProgerssHide$ = this.actions$.pipe(
        ofType('LANDING_SUBSCRIBE_ERROR'),
        map((data: any) => ({ type: 'PROGRESSBAR_HIDE' })),
    )

    constructor(
        private actions$: Actions,
        private http: HttpClient,
        private router: Router,
        private store: Store<any>,
        private db: AngularFirestore,
    ) { }

}
