
const initialState: any = {
    form: {},
    isVisible: {
        form: true,
    }
}

export function reducer(state = initialState, action) {
    switch (action.type) {

        case 'LANDING_SUBSCRIBE_SUCCESS':
            return {
                ...state,
                isVisible: {
                    form: false
                }
            }

        default:
            return state;
    }
}